@import "../../utils/mixins";

.navbar-user {
  display: flex;
  width: 100%;

  &__item {
    position: relative;
    padding: 24px 25%;
  

  @include onTablet {
    padding: 24px; 
    border-right: 1px solid #E2E6E9;   
  }

  @include onDesktop {
    padding: 24px; 
    border-right: 1px solid #E2E6E9;
  }
}

  &__hard {
    position: absolute;
    top: 17px;
    right: 44%;
   
    width: 14px;
    height: 14px;
    border-radius: 50%;

    background-color: #476DF4;
    
    @include onTablet {
      top: 17px;
      right: 18px; 
    }

    @include onDesktop {
      top: 17px;
      right: 18px;
    }
      
  }

   &__hard-item {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 100;
    font-family: "Montserrat", Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 11px;
    line-height: 12px;
    text-align: center;
    color: #FFFFFF;

  }
}